import axios from 'axios';
import { FETCH_USER } from './types';

export const fetchUser = () => (dispatch) => {
  axios
    .get('/auth/current_user')
    .then(res => dispatch({ type:FETCH_USER , payload: res.data.user }))
    .catch(res => dispatch({ type: FETCH_USER, payload: false }));
}

export const signupUser = (signupFormValues, history) => (dispatch) => {
  axios
    .post('/auth/signup', signupFormValues)
    .then(res => {
      dispatch({ type:FETCH_USER , payload: res.data.user });
      // Redirect the user to the home page
      history.push('/');
    })
    .catch(res => dispatch({ type: FETCH_USER, payload: false }));
}

export const loginUser = (loginFormValues, history) => (dispatch) => {
  axios
    .post('/auth/signup', loginFormValues)
    .then(res => {
      dispatch({ type:FETCH_USER , payload: res.data.user });
      // Redirect the user to the home page
      history.push('/');
    })
    .catch(res => dispatch({ type: FETCH_USER, payload: false }));
}
